<div>

</div>
</template>

<script>
export default {
name:'hello',
data(){
	return{
		msg:'Welcome to Your Vue.js App'
	}
},
created:function(){
	this.HelloAxios();
},
methodes:{
	HelloAxios(){
		this.$http.get("https://m.toutiao.com/list/?tag=__all__&ac=wap&count=20&format=json_raw&as=A1D559AF43EFDDF&cp=59F3CF4DDD8F1E1").then(m=>console.log(m.data))
	
},
HelloAxiosPost(val){
	let str="value="+val
	this.$http.post("https://m.toutiao.com/list/?tag=__all__&ac=wap&count=20&format=json_raw&as=A1D559AF43EFDDF&cp=59F3CF4DDD8F1E1",str).then(m=>console.log(m.data))
}	
}
}
</script>

