<template>
	<div id="dingwei">
		<p class="wz">您所在的位置：</p>
		 <div style="color: red;">经度：<span id="jingdu"></span></div>
<div style="color: blue;">纬度：<span id="weidu">{{a}}</span></div>
<div style="color: gold;">属于：<span id="guojia">{{b}}</span>-<span id="sheng"></span> </div>
<a href="https://denghaoliang.github.io/aaaa/jwd.html
"><button @click="getLocation" id="button">位置</button></a> 
	<h4 @click="rtn">返回</h4>

	</div>
 
 
	
	
</template>

<script>
	
	export default {
	       data(){
			return{
				jd:'',
				wd:"",
				a:'',
				b:''
			}
		}, 
		showPosition(position){
		                 a=position.coords.longitude
			             b=position.coords.latitude
			 jd=Number(position.coords.longitude) 
			 wd=Number(position.coords.latitude) 	
			       },
		methods:{
			rtn(){
				return this.$router.go(-1)
			},
			getLocation(){
		    navigator.geolocation.getCurrentPosition(this.showPosition)
		    
		}	
	  }
		
	}
	
</script>
<!--<script src="../../static/js/ajax.js"></script>-->
<style>
	#dingwei{
		width: 100%;
		height: 100%;
		background-color:rgba(103, 146, 142,0.6);
		position: absolute;
		z-index: 50;
	}
	.wz{
		margin-top: 100px;
	}
</style>


