<template>
	<div id="tianqi">
		<p><iframe width="350rem" scrolling="no" height="100" frameborder="0" allowtransparency="true" src="http://i.tianqi.com/index.php?c=code&id=12&color=%23FFC000&icon=1&num=3&site=14"></iframe></p>
		<p @click="rtn">返回</p>
	</div>
</template>

<script>
	export default {
		methods:{
			rtn(){
				return this.$router.go(-1)
			}
		}
		
	}
</script>

<style>
	#tianqi{
		width: 100%;
		height: 100%;
		background-color:rgba(103, 146, 142,0.6);
		position: absolute;
		z-index: 50;
		
	}
	#tianqi>p:nth-child(1){
		margin-top: 75px;
		cursor: pointer;
	}
	#tianqi>p:nth-child(2){
	   cursor:pointer
	}
</style>


