  		<i>{{x.date}}</i>
  		<img :src="x.thumbnail_pic_s"/>
  	</div>
  </a>
</div>
</template>

<script scoped>
	import aip1 from "../../static/js/shuju1.js"
//import swiper from '../../static/js/one.js';
export default {
	data(){
		return {
			aip :aip1.result.data,
			msg:'Welcome to Your Vue.js App' 
		}
	},
	methods:{
		p(){
			console.log(aip1.result.data)
		}
	},
	created(){
		this.p()
	}
         
}
</script>

<style scoped>
  @import '../../static/css/one.css';
	
</style>


