import Vue from 'vue'
import Router from 'vue-router'
//import One from "@/components/one"
const One = (resolve) => {
  import('@/components/one').then((module) => {
    resolve(module)
  })
}
import Two from "@/components/two"
import Shipin from "@/components/shipin"
import Yule from "@/components/yule"
import Tiyu from "@/components/tiyu"
import Junshi from "@/components/junshi"
import Keji from "@/components/keji"
import Caijing from "@/components/caijing"
import Btv from "@/components/BTV"
import Shenghuo from "@/components/shenghuo"
import Guoji from "@/components/guoji"
import Tianqi from "@/chajian/tianqi"
import Dingwei from "@/chajian/dingwei"

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/one',
      name:"one",
      component:One
    },{
    	path:"*",
    	redirect:{"name":'one'}
    },{
    	path:"/two",
    	component:Two
    },{
    	path:"/shipin",
    	component:Shipin
    },{
    	path:"/yule",
    	component:Yule
    },{
    	path:"/tiyu",
    	component:Tiyu
    },{
    	path:"/junshi",
    	component:Junshi
    },{
    	path:"/keji",
    	component:Keji
    },{
    	path:"/caijing",
    	component:Caijing
    },{
    	path:"/BTV",
    	component:Btv
    },{
    	path:"/shenghuo",
    	component:Shenghuo
    },{
    	path:"/guoji",
    	component:Guoji
    },{
    	path:"/tianqi",
    	component:Tianqi
    },{
    	path:"/dingwei",
    	component:Dingwei
    }
  ]
})



